:host {
  .table-compact {
    height: auto;
  }

  .table-compact ::ng-deep .mat-mdc-header-cell.mat-column-selectionCheckbox {
    width: 52px;
  }

  textarea {
    resize: none;
  }

  mat-form-field {
    padding: 10px 15px;
  }
  mat-form-field.coordinates {
    width: 110px;
  }
  mat-form-field.coordinates2 {
    width: 100px;
    padding: 0px 10px 0px 3px;
  }
  mat-form-field.igo-input-container {
    width: 60%;
    padding: 0px 15px;
  }

  .radius-unit {
    display: flex;
    width: 90%;
    margin-left: 2px;
    padding: 5px;
    mat-form-field.radius-field {
      display: flex;
      flex-flow: column nowrap;
      width: 100%;
    }
  }

  .title-container {
    padding: 10px;
  }

  .title {
    margin-left: 5px;
    font-size: initial;
  }

  .mat-mdc-radio-group {
    display: flex;
    flex-direction: column;
  }

  .mat-mdc-radio-button {
    display: inline-flex;
    position: relative;
    margin-left: 12px;
  }
}
